@import url(https://fonts.googleapis.com/css2?family=Baloo+2&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#container{
  height: 100%;
  opacity: 1;
  -webkit-animation:fadeIn;
          animation:fadeIn;
  -webkit-animation-duration: 10s;
          animation-duration: 10s;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;

  -webkit-animation: fadeIn 10s;
  -moz-animation: fadeIn 10s;
  -o-animation: fadeIn 10s;
  -ms-animation: fadeIn 10s;

}
@keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

@-webkit-keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

@-webkit-keyframes fadein {
  0% {opacity:0}
  100% { opacity:1}
}

@keyframes fadein {
  0% {opacity:0}
  100% { opacity:1}
}
.px-3 {
    padding-right: 0!important;
    padding-left: 0!important;
    width: 100%!important;
    height: 100%!important;
}

#main_container {
    width: 100%;
    height: 100%;
    margin-right: auto;
    margin-left: auto;
    -webkit-animation:fadeInBackground;
            animation:fadeInBackground;
    background-image: url(/homepage/static/media/background_image.cdbfe560.jpg);
    -webkit-animation-duration:9s;
            animation-duration:9s;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
        /* Center form controls */
    display: inline-block;
    justify-content: center;
    align-items:center;

        /* Override Bootstrap's 100% width for form controls */
        /* width: auto; */
}
@-webkit-keyframes fadeInBackground {
    0% { opacity:0}
    100% { opacity:1}
}
@keyframes fadeInBackground {
    0% { opacity:0}
    100% { opacity:1}
}

.jumbotron {
    /* padding-top: 20%; */
    position: relative;
    text-align: right;
    color: white;
    -webkit-animation: nameAnimation;
            animation: nameAnimation;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
    -webkit-animation-duration: 4.5s;
            animation-duration: 4.5s;
    top: 20%;
    font-size: 60px;
    padding-right:12px;
    
}

@-webkit-keyframes nameAnimation {
    from {color:white;}
    to {color: #212529;}
}

@keyframes nameAnimation {
    from {color:white;}
    to {color: #212529;}
}


.jumbotron2 {
    position: relative;
    color: #212529;
    text-align: right;
    top: 19%;
    padding-bottom: 2.5%;
    -webkit-animation: descriptionAnimation;
            animation: descriptionAnimation;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-duration: 7s;
            animation-duration: 7s;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    font-size: 36px;
    padding-right: 15px;
}

@-webkit-keyframes descriptionAnimation {
    0% {color: transparent; left: -50%;}
    25% { color:transparent; left: -25%;}
    50% { color:#4c555e; left: -12.5%; }
    75% { left: 0%; }
    100% { color: #212529; text-align:right ;}
}

@keyframes descriptionAnimation {
    0% {color: transparent; left: -50%;}
    25% { color:transparent; left: -25%;}
    50% { color:#4c555e; left: -12.5%; }
    75% { left: 0%; }
    100% { color: #212529; text-align:right ;}
}

.pageCushion {
    width: 100%;
    height: 40px;
    background-color: #110f16;
    display: flex;
    justify-content: center;
    position: absolute;
    margin: 0;
    padding: 0;
    bottom: 0;
}

#down_arrow {
    background-image: url(data:image/gif;base64,);
    margin: 0;
    width: 33px;
    height:33px;
    background-repeat: no-repeat;
    -webkit-transform:translate(0%,0px);
            transform:translate(0%,0px);
    align-items:none;
}


#borderContainer {
    margin-top:0;
    padding:0;
    border:0;
    position:relative;
    height: 100%;
}

#navbar {
    padding-top: 0;
    margin-top: 0;
    border-top: 0;
    border:0;
    max-width: 100%;
    background: white;
    display: block;
    height: 40px;
}

.nav-link {
    color: #3a3a3a; 
}

#copyright {
    padding-right: 3px;
    color: rgba(255, 255, 255, 0.151);
    padding-left: 15px;
}

#ccLink {
    color: rgba(255, 255, 255, 0.151);
    text-decoration: none;
    border-bottom: dotted 1px;
    padding-right: 15px;
}

#listItems {
    color: black;
    display: inline-block;
    position: relative;
    text-align: center;
    vertical-align: middle;
    margin-bottom: 0;
    padding-left: 0;
    -webkit-transform:translate(-30%,112%);
            transform:translate(-30%,112%);
    width:100%;
    -webkit-animation:lefttoright;
            animation:lefttoright;
    -webkit-animation-duration: 5.25s;
            animation-duration: 5.25s;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
}
@-webkit-keyframes lefttoright {
    from {-webkit-transform:translate(100%,125%);transform:translate(100%,125%)}
    to {-webkit-transform:translate(-20%,125%);transform:translate(-20%,125%)}
}
@keyframes lefttoright {
    from {-webkit-transform:translate(100%,125%);transform:translate(100%,125%)}
    to {-webkit-transform:translate(-20%,125%);transform:translate(-20%,125%)}
}



.links {
    text-decoration: none; 
    background-color: none;
    color: black;
}



@media screen and (max-width: 500px) {
    #ccLink {  
        font-size:7.5px;
        padding-right: 0px;
    }
    .jumbotron {
        font-size: 40px;
        line-height: 0.9;
        -webkit-transform:translate(0,-30%);
                transform:translate(0,-30%);
    }

    .jumbotron2{
        font-size: 21px;
        -webkit-transform:translate(0,-25%);
                transform:translate(0,-25%);

    }
    #listItems {
        top: 5%;
    }

    #copyright {
        font-size: 7.5px;
        padding-right: 0px;
        padding-left: 0px;
    }

    #listItems {
        -webkit-transform:translate(-20%,60%);
                transform:translate(-20%,60%);
        -webkit-animation:lefttoright;
                animation:lefttoright;
        -webkit-animation-duration: 5.25s;
                animation-duration: 5.25s;
        -webkit-animation-timing-function: linear;
                animation-timing-function: linear;
    }
    @-webkit-keyframes lefttoright {
        from {-webkit-transform:translate(100%,60%);transform:translate(100%,60%)}
        to {-webkit-transform:translate(-20%,60%);transform:translate(-20%,60%)}
    }
    @keyframes lefttoright {
        from {-webkit-transform:translate(100%,60%);transform:translate(100%,60%)}
        to {-webkit-transform:translate(-20%,60%);transform:translate(-20%,60%)}
    }
}

@media screen and (min-width: 1024px) {

    #listItems {
        top: -10%;
    }
    #listItems {
        -webkit-transform:translate(0%,50%);
                transform:translate(0%,50%);
        -webkit-animation:lefttoright;
                animation:lefttoright;
        -webkit-animation-duration: 5.25s;
                animation-duration: 5.25s;
        -webkit-animation-timing-function: linear;
                animation-timing-function: linear;
    }
    @-webkit-keyframes lefttoright {
        from {-webkit-transform:translate(100%,80%);transform:translate(100%,80%)}
        to {-webkit-transform:translate(0%,80%);transform:translate(0%,80%)}
    }
    @keyframes lefttoright {
        from {-webkit-transform:translate(100%,80%);transform:translate(100%,80%)}
        to {-webkit-transform:translate(0%,80%);transform:translate(0%,80%)}
    }

    .jumbotron{
        top: 19%;
    }

    .jumbotron2{
        top: 18.5%;
    }
}    

@media screen and (min-width: 1000px) {
    #listItems {
        -webkit-transform:translate(-3%,180px);
                transform:translate(-3%,180px);
    }
}
